.main {
    display: flex;
    gap: 20%;

}

.round {
    height: 30px;
    width: 30px;
    background-color: #13d7a1;
    border-radius: 30px;
    border: #333333 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
}

.round div {
    /*margin: 5px auto;*/
    height: 20px;
    width: 20px;
    background-color: rgb(0, 0, 0);
    border-radius: 30px;
    border: #333333 1px solid;
}

.title {
    display: flex;
    justify-content: center;
}